import React from 'react'

const Bar = ({ title, p }) => {
    return (
        <div className=" flex flex-col  pl-10 divider-r my-6 ">
            <h1 className="font-source text-h3 text-white">{title}</h1>
            <div className="flex flex-row gap-1 mt-4 ml-2">
                <div className={`w-6 h-2  ${p >= 1? 'bg-accent' : 'bg-gray-700' }`}></div>
                <div className={`w-6 h-2  ${p >= 2? 'bg-accent' : 'bg-gray-700' }`}></div>
                <div className={`w-6 h-2  ${p >= 3? 'bg-accent' : 'bg-gray-700' }`}></div>
                <div className={`w-6 h-2  ${p >= 4? 'bg-accent' : 'bg-gray-700' }`}></div>
                <div className={`w-6 h-2  ${p >= 5? 'bg-accent' : 'bg-gray-700' }`}></div>
                
            </div>
        </div>
    )
}

export default Bar
