import React from 'react'
import CustomScrollbars from '../components/CustomScrollbars'

const EducationPage = () => {
    return (
        <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
        <div className=" cursor-default max-w-6xl mx-auto h-auto flex flex-col px-10  py-10 overflow-x-hidden overflow-y-auto divider-r">
            <h1 className="text-d1 text-white font-source font-bold" >Education<span className="text-accent">_</span></h1>

            <div className="flex flex-col divider-l  mx-10 mt-10 ">
                <div className=" flex flex-col  pl-10 divider-r ">
                    <h1 className="font-source text-h1 text-white">B.tech (I.T.)</h1>
                    <p className="font-source text-body text-white">Bharati Vidyapeeth University, Pune</p>
                    <p className="font-source text-h6 text-white my-2" >2019-<span className="text-accent">Present</span></p>
                </div>
                <div className="flex flex-col  pl-10 divider-r mt-10 ">
                    <h1 className="font-source text-h1 text-white">Intermediate</h1>
                    <p className="font-source text-body text-white">N.J.S. College, Bihar</p>
                    <p className="font-source text-h6 text-white my-2" >2017-2019</p>
                </div>
                <div className="flex flex-col  pl-10 divider-r mt-10">
                    <h1 className="font-source text-h1 text-white">Matriculation</h1>
                    <p className="font-source text-body text-white">Aadarsh Vikash Vidyalay, Bihar</p>
                    <p className="font-source text-h6 text-white my-2" >2017</p>
                </div>

                

            </div>
            <div className="divider-b h-10 w-full">

                </div>
            
        </div>
        </CustomScrollbars>
    )
}

export default EducationPage
