import React from 'react'
import logo from './Rakt/logo.svg'
import branding from './Rakt/branding.svg'
import Button from '../../components/Button'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './Rakt/s1.jpg'
import s2 from './Rakt/s2.jpg'
import s3 from './Rakt/s3.jpg'

const Rakt = () => {
    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full px-10 py-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

        <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
            <div className=" flex flex-row gap-6 divider-b pb-4">
                <img src={logo} alt="" />
                <div className=" flex flex-col self-center gap-3" >
                    <img src={branding} alt="" />
                    <Button title="Playstore" link="https://play.google.com/store/apps/details?id=dev.appsef.bludapp" />
                </div>

            </div>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


            <p className=" font-source text-body text-white mt-2 max-w-xl  overflow-y-auto " >

                Rakt Community was developed to create a community of sorts where every blood bank in the nation can connect with each other seamlessly to form a network of medical workers and doctors, allowing maximum transparency in the work and taking the work of Gods, or doctors in this case, to maximum efficiency.
                <br />
                <br />
                I was the core developer in this project under appsef. Rakt uses Firebase (BaaS) and written in java.
                <br />
                <br />
                Rakt allows you to filter blood and plasma donors based on blood group and shows donors near your location or you can search for a particular one. <br /> <br />
                This project saved multiple lives in the pandemic and we got interviewed twice on national radio (All India Radio) for that. 
                <br />
                <br />
            </p>
            </CustomScrollbars>

        </section>
        <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
            <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />

                </div>
            </CustomScrollbars>

        </section>
    
    </div>
    )
}

export default Rakt
