import React, { useState } from 'react'
import './App.css';
import MenuSection from './components/MenuSection';
import TopBar from './components/TopBar';
import BioPage from './pages/BioPage';
import EducationPage from './pages/EducationPage';
import NoPage from './pages/NoPage';
import Bvpxp from './pages/projects/Bvpxp';
import CryptoWhaler from './pages/projects/CryptoWhaler';
import Doc from './pages/projects/Doc';
import Folio from './pages/projects/Folio';
import KeyNote from './pages/projects/KeyNote';
import Rakt from './pages/projects/Rakt';
import Stayug from './pages/projects/Stayug';
import SkillsPage from './pages/SkillsPage';
import WorkPage from './pages/WorkPage';

function App() {

  const [selectedTab, setSelectedTab] = useState(0)

  const setPage = (tabId) => {
    switch(tabId) {
      case 0: return <BioPage />
      case 1: return <EducationPage />
      case 2: return <WorkPage />
      case 3: return <SkillsPage />
      case 4: return <CryptoWhaler />
      case 5: return <Rakt />
      case 6: return <Stayug />
      case 7: return <Doc />
      case 8: return <KeyNote />
      case 9: return <Bvpxp />
      case 10: return <Folio />
      default: return <NoPage />
    }
  }

  return (
    <div className="flex flex-col md:grid md:grid-cols-12 md:grid-flow-row gap-2 mx-auto overflow-hidden  w-screen h-screen bg-black">
      <TopBar />
      <div className="hidden md:block col-span-3 lg:col-span-2 divider-r col-start-1 lg:col-start-1">
        <MenuSection selectedTab={selectedTab} setSelectedTab={setSelectedTab}/>
        
      </div>
      <div className="w-full h-full md:col-span-9 lg:col-span-10 overflow-y-auto overflow-x-hidden" >
          {setPage(selectedTab)}
        </div>
      
    </div>
  );
}

export default App;
