import React from 'react'

const Button = ({ title, link }) => {
    return (
        <div className={`cursor-pointer group relative w-min py-1 h-8 pr-3 mx-2 `}>
                <a href={link} rel="noreferrer" target="_blank" className="text-white tracking-wider underline opacity-90 group-hover:opacity-100 self-center text-sb font-source font-bold group-hover:text-accent">{title}</a>
                <div className="border-t-3 border-r-3 border-accent w-2 h-2 absolute top-2 right-1 " ></div>
            </div>
    )
}

export default Button
