import React from 'react'

const NavButton = ({ nav, selectedTab, setSelectedTab }) => {

    const selected = selectedTab === nav.id ? true : false

    const handleClick = () => {
        setSelectedTab(nav.id)
    }

    return (
        <button onClick={handleClick} className="cursor-pointer h-min w-full p-2 flex flex-row group my-3 ">
            <div className={`w-1 h-3 bg-accent self-center mr-2 ${selected ? 'visible' : 'invisible'} `}></div>
            <p  className={` ${selected ? 'text-accent' : 'text-white'} text-left tracking-wider text-sb font-source font-medium group-hover:text-accent `}>
            {nav.title}</p>
        </button>
    )
}

export default NavButton
