import React from 'react'
import logo from './Stayug/logo.svg'
import branding from './Stayug/branding.svg'
import Button from '../../components/Button'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './Stayug/s1.jpg'
import s2 from './Stayug/s2.jpg'
import s3 from './Stayug/s3.jpg'

const Stayug = () => {
    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full px-10 py-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

        <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
            <div className=" flex flex-row gap-6 divider-b pb-4">
                <img src={logo} alt="" />
                <div className=" flex flex-col self-center gap-3" >
                    <img src={branding} alt="" />
                    <Button title="Playstore" link="https://play.google.com/store/apps/details?id=com.stayug.app" />
                </div>

            </div>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


            <p className=" font-source text-body text-white mt-2 max-w-xl overflow-y-auto " >

                Another product made while keeping the needs of students in mind is Stayug. Stayug is a client project which aims to create an e-commerce application centered around the sale and distribution of stationary items. 
                <br />
                <br />
                It is often seen that students would be completing their assignments and homework and would have to salvage for material at the last hour.
                <br />
                <br />
                Stayug plans to get away with this problem by making the delivery of these products super quick to the doorsteps. Ordering these same products from other websites might take hours, and sometimes days, and its not relevant if so much time has passed. Stayug however, plans to do the deed in around an thus making it a reliable partner for any student.
                <br />
                <br />
            </p>
            </CustomScrollbars>

        </section>
        <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
            <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />

                </div>
            </CustomScrollbars>

        </section>
    
    </div>
    )
}

export default Stayug
