import React from 'react'
import logo from './bvpxp/logo.svg'
import branding from './bvpxp/branding.svg'
import Button from '../../components/Button'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './bvpxp/s1.jpg'
import s2 from './bvpxp/s2.jpg'
import s3 from './bvpxp/s3.jpg'

const Bvpxp = () => {
    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full px-10 py-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

        <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
            <div className=" flex flex-row gap-6 divider-b pb-4">
                <img src={logo} alt="" />
                <div className=" flex flex-col self-center gap-3" >
                    <img src={branding} alt="" />
                    <Button title="Playstore" link="https://play.google.com/store/apps/details?id=com.datacraves.bvpxp" />
                </div>

            </div>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


            <p className=" font-source text-body text-white mt-2 max-w-xl  overflow-y-auto " >

                BVPXP is an app endorsed by the Bharati Vidyapeeth College of Engineering, Pune and is a repository for all the previous year question papers by the university. 
                <br />
                <br />
                The app was made as a helping hand to our fellow students who struggled to find relevant material and a handy tool. The app focuses on hosting individual exam papers, thus making it ideal for any branch and any semester. 
                <br />
                <br />
                BVPXP has features like in-built PDF viewer, offline viewing and downloading, and night mode for better accessibility in low light conditions. All these features paired with the ability to search and sort the papers by branch, subject and semester makes it a far better product than the older versions.  
                <br /> 
                <br />
                While the app is 'complete' in terms of development, me and my team would keep on adding new question papers as we come across them.
                <br />
                <br />
            </p>
            </CustomScrollbars>

        </section>
        <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
            <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />

                </div>
            </CustomScrollbars>

        </section>
    
    </div>
    )
}

export default Bvpxp
