import React from 'react'
import NavButton from './NavButton'
import { Disclosure } from '@headlessui/react'
import CustomScrollbars from './CustomScrollbars'

const MenuSection = ( { selectedTab, setSelectedTab }) => {

    const nav_menu = [
        {title: 'Bio', id: 0 },
        {title: 'Education', id: 1 },
        {title: 'Experience', id: 2 },
        {title: 'Skills', id: 3 },
    ]

    const project_menu = [
        {title: 'CryptoWhaler', id: 4 },
        {title: 'Rakt Community', id: 5 },
        {title: 'Stayug (Ecommerce)', id: 6 },
        {title: 'Doc ', id: 7 },
        {title: 'KeyNote ', id: 8 },
        {title: 'bvpxp ', id: 9 },
        {title: 'Folio', id: 10 },
    ]

    return (
        <div className="w-30 h-screen flex flex-col justify-start px-4">
            <p className=" cursor-default text-body font-source text-gray-700 mx-2 divider-b">About Me 
                <svg className="inline self-center fill-current text-gray-700" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path fill="none" d="M0 0h24v24H0z"/><path d="M12 15l-4.243-4.243 1.415-1.414L12 12.172l2.828-2.829 1.415 1.414z" /></svg>
            </p>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200} >
            <nav className="overflow-y-auto overflow-x-hidden pb-10">
                {/* General Menus */}
                {nav_menu.map((nav) => 
                    <NavButton nav={nav} selectedTab={selectedTab} setSelectedTab={setSelectedTab} />
                 )}

                {/* Projects */}
                <Disclosure>
                    {({open}) => (
                        <>
                            <Disclosure.Button className=" text-white tracking-wider text-sb font-source font-medium ">
                                <div className="cursor-pointer h-8 w-min pr-2 pt-2 pb-2 flex flex-row group my-3 ">
                                    <svg className={`fill-current text-white ${open? "transform rotate-90" : ""}`} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path fill="none" d="M0 0h24v24H0z"/><path d="M12.172 12L9.343 9.172l1.414-1.415L15 12l-4.243 4.243-1.414-1.415z"/></svg>
                                    <button className={`text-center text-white tracking-wider text-sb font-source font-medium group-hover:text-accent`}>Projects</button>
                                </div>
                            </Disclosure.Button>
                            <Disclosure.Panel className="text-gray-500 ml-3 divider-l">
                                    {project_menu.map((nav) => 
                                    <NavButton nav={nav} selectedTab={selectedTab} setSelectedTab={setSelectedTab} />
                                    )}
                            </Disclosure.Panel>
                        </>
                    )}
                </Disclosure>
            </nav>
            </CustomScrollbars>

        </div>
    )
}

export default MenuSection
