import React from 'react'
import Button from './Button'
import logo from '../assets/logo.svg'

const TopBar = () => {
    return (
        <div className="top-0 sticky w-screen z-10 divider-b bg-black h-8 flex flex-row justify-end gap-6">

            <img className="absolute left-5 top-2 bottom-2" src={logo} alt="logo" />

            {/* <Button title="linkedIn" link="https://www.linkedin.com/in/alok-j-kashyap-62b861192/"/>
            <Button title="twitter" link="https://twitter.com/alokjkashyap" /> */}
            <Button title="github" link="https://github.com/alokjkashyap"/>

        </div>
    )
}

export default TopBar
