import React from 'react'
import logo from './KeyNote/logo.svg'
import branding from './KeyNote/branding.svg'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './KeyNote/s1.jpg'
import s2 from './KeyNote/s2.jpg'
import s3 from './KeyNote/s3.jpg'
import s4 from './KeyNote/s4.jpg'
import s5 from './KeyNote/s5.jpg'

const KeyNote = () => {
    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full px-10 py-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

        <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
            <div className=" flex flex-row gap-6 divider-b pb-4">
                <img src={logo} alt="" />
                <div className=" flex flex-col self-center gap-3" >
                    <img src={branding} alt="" />
                    {/* <Button title="Playstore" link="https://play.google.com/store/apps/details?id=com.stayug.app" /> */}
                    <p className="font-source text-sb font-medium text-gray-500" >Personal Project</p>
                </div>

            </div>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


            <p className=" font-source text-body text-white mt-2 max-w-xl  overflow-y-auto " >

            KeyNote is a note keeper app. It is different from it competitors in the way it stores the notes. It keeps notes in a folder with a custom label and one of the six colour tags and icons which define category/priority.
                <br />
                <br />
                It also records every activity in any note, shows you summary like the number of folders and notes and overview of your notes and folders. It also lets you share a custom image of your note.
                <br />
                <br />
                This app was created to keep all my project details and ideas isolated from each other. KeyNote performs well in this function.
                <br />
                <br />
                It does not have any backend yet meaning it operates completely offline. KeyNote is written in Kotlin and based on MVI.<br /> <br />
                
            </p>
            </CustomScrollbars>

        </section>
        <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
            <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />
                    <img className="rounded-xl" src={s4} alt="Screen 4" />
                    <img className="rounded-xl" src={s5} alt="Screen 5" />

                </div>
            </CustomScrollbars>

        </section>
    
    </div>
    )
}

export default KeyNote
