import React from 'react'
import logo from '../../assets/cryptowhaler.svg'
import branding from '../../assets/cwhlr_title.svg'
import Button from '../../components/Button'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './Cryptowhaler/s1.jpg'
import s2 from './Cryptowhaler/s2.jpg'
import s3 from './Cryptowhaler/s3.jpg'
import s4 from './Cryptowhaler/s4.jpg'

const CryptoWhaler = () => {

    // const renderThumb = ({ style, ...props }) => {
    //      const thumbStyle = {
    //         borderRadius: 0,
    //         backgroundColor: 'rgba(0, 255, 197, 0.8)'
    //     };
    //     return <div style={{ ...style, ...thumbStyle }} {...props} />;
    // };

    // const CustomScrollbars = props => (
    //     <Scrollbars
    //         renderThumbHorizontal={renderThumb}
    //         renderThumbVertical={renderThumb}
    //         {...props}
    //     />
    // );

    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full px-10 py-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

            <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
                <div className=" flex flex-row gap-6 divider-b pb-4">
                    <img src={logo} alt="" />
                    <div className=" flex flex-col self-center gap-3" >
                        <img src={branding} alt="" />
                        <Button title="Playstore" link="https://play.google.com/store/apps/details?id=com.appsef.cryptowhaler" />
                    </div>

                </div>

                <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


                <p className=" font-source text-body text-white mt-2 max-w-xl overflow-y-auto " >

                    CryptoWhaler is an app aimed towards providing knowledge of cryptocurrency and related topics. The app features articles, shorts and other content in several languages both recent and established. 
                    <br />
                    <br />
                    CryptoWhaler uses Firebase (BaaS), based on MVI (architecture pattern), written in Kotlin and has Dependency Injection implementation using Dagger Hilt which prevents the app from crashing. This projects does not feature any ads and never will. 
                    <br />
                    <br />
                    The app provides night mode for easy usage in low light conditions. The user can also save the content for offline viewing providing ease of access. The multilingual aspect of this app and a slew of other features make it easy to use among a wide range of crypto enthusiasts.  <br /> <br />
                    My team has plans centered around providing more content for this app and realising the full potential of this project.
                    <br />
                    <br />
                </p>
                </CustomScrollbars>

            </section>
            <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
                <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
                <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />
                    <img className="rounded-xl" src={s4} alt="Screen 4" />
                    

                </div>
                </CustomScrollbars>

            </section>
        
        </div>
    )
}

export default CryptoWhaler
