import React from 'react'
import logo from './Doc/logo.svg'
import branding from './Doc/branding.svg'
import CustomScrollbars from '../../components/CustomScrollbars'

import s1 from './Doc/s1.jpg'
import s2 from './Doc/s2.jpg'
import s3 from './Doc/s3.jpg'

const Doc = () => {
    return (
        <div className="cursor-default overflow-hidden max-w-6xl mx-auto h-full py-10 px-10 gap-1 divider-r grid grid-cols-5 grid-flow-row">

        <section className="w-full col-span-3 h-full flex flex-col gap-4 overflow-hidden">
            <div className=" flex flex-row gap-6 divider-b pb-4">
                <img src={logo} alt="" />
                <div className=" flex flex-col self-center gap-3" >
                    <img src={branding} alt="" />
                    {/* <Button title="Playstore" link="https://play.google.com/store/apps/details?id=com.stayug.app" /> */}
                    <p className="font-source text-sb font-medium text-gray-500" >Under Development</p>
                </div>

            </div>

            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>


            <p className=" font-source text-body text-white mt-2 max-w-xl overflow-y-auto " >

            Doc is designed to assist small businesses in the phone repair business. It helps individuals in keeping track of their repairing cases registered under a particular shop. This app can also raise a request for repair and its estimated cost.
                <br />
                <br />
                It is written in Kotlin and is based on the MVI architecture pattern with Firebase as Backend (BaaS). It alerts the user on any update regarding their open cases.
                <br />
                <br />
                It is one of the solo/personal non-paid projects I've recently worked on.
                
                <br />
                <br />
            </p>
            </CustomScrollbars>

        </section>
        <section className="w-full col-span-2 h-full flex flex-col divider-l overflow-hidden sticky">
            <h6 className="text-white font-source text-h6 mx-4 divider-b" >Screens</h6>
            <CustomScrollbars autoHide autoHideTimeout={500} autoHideDuration={200}>
                <div className=" grid grid-cols-2 grid-flow-row gap-4 mx-4 self-center my-4 overflow-x-hidden overflow-y-auto " >
                    <img className="rounded-xl" src={s1} alt="Screen 1" />
                    <img className="rounded-xl" src={s2} alt="Screen 2" />
                    <img className="rounded-xl" src={s3} alt="Screen 3" />

                </div>
            </CustomScrollbars>

        </section>
    
    </div>
    )
}


export default Doc
